.input-field {
  color: var(--COLOR_DARK);
  font-size:3dvh;
  text-shadow:0.3svh 0.3svh 0.6svh rgba(0, 0, 0, 0.5);
  background-color:white;
  border-radius:0.25em;
  box-shadow:0.3svh 0.3svh 0.6svh rgba(0, 0.5, 0, 0.5);
  border:none;
  text-align:center;
  max-width:10dvh;
  /* margin:0 auto; */
  /*margin-top:3dvh;*/
}

.input-field:hover {
  animation: pulse 2s infinite;
}

/* LLM-generated */
@keyframes pulse {
  0% { box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.7); }
  70% { box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.7); }
  100% { box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.7); }
}

/* Makes it so disabled input fields are not greyed out. */
.input-field-table-division:disabled {
  background-color: transparent;
  opacity: 1;
  color: white;
}

.input-box-table-division {
  position: absolute;
  max-width: 73%;
  bottom: 1dvh;
  left: 3dvh;
  /* transform: translateX(-60%); */
  /* background-color: red; */
  z-index: 1;
}

